body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 2000px;
  /* To make the page scrollable */ }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  scroll-behavior: smooth; }

html,
body {
  overflow: auto;
  /* Ensure no restriction on body overflow */
  height: 100%; }

figure {
  margin: 0; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", sans-serif; }

a, li, p {
  font-family: "Roboto Slab", sans-serif; }

a {
  text-decoration: none; }

[aria-current="page"] {
  color: #9B1C31 !important; }

ul {
  list-style: none; }

.content {
  flex: 1; }

.to-reveal {
  opacity: 0;
  position: relative;
  top: 20px; }

.revealed {
  transition: 0.6s cubic-bezier(0.64, 0.04, 0.35, 1);
  opacity: 1;
  position: relative;
  top: 0; }

.dark-bg {
  transition: 300ms;
  visibility: hidden; }

.show-dark-bg {
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  visibility: visible; }



